import { StyledParagraph } from '../TextOnImage/style';
import { Wrapper, Title, StyleParagraph, TextWrapper, ImageWrapper } from './style';

const TextAndPictureEachOther = ({ nameOfImageFromPublicWithExtension }) => {
  return (
    <Wrapper>
      <TextWrapper>
        <Title>Wypeniona zieleni oaza</Title>
        <StyleParagraph>
          Lene Zacisze w Gogoczowie, jak sama nazwa wskazuje, to przede wszystkim okolica, ktra
          zachwyca niezwykle bogat faun i flor. Inwestycj otaczaj pene wyjtkowego uroku
          zalesienia, ki i pola. W bezporednim ssiedztwie znajduje si tylko kilka domw
          jednorodzinnych, dziki czemu masz zapewnion idealn cisz i niezmcony spokj. Otoczenie
          zieleni gwarantuje zdrowy klimat oraz czyste powietrze o kadej porze roku.
        </StyleParagraph>
        <StyledParagraph>
          W Lenym Zaciszu o poranku bdziesz cieszy si piewem ptakw i oddycha pen piersi,
          chonc cudowny zapach lasu. Wieczorem rozsidziesz si wygodnie, by podziwia najpierw
          malowniczy zachd soca, a nastpnie obserwowa rozgwiedone niebo. To idealnie miejsce
          zarwno do tego, by odpocz od zgieku miasta, jak i pracowa w penym skupieniu. W
          okolicy nie brakuje rwnie punktw usugowych czy sklepw, w ktrych zrobisz codzienne
          zakupy.
        </StyledParagraph>
      </TextWrapper>
      <ImageWrapper
        style={{
          backgroundImage: `url(${
            process.env.PUBLIC_URL + `/img/${nameOfImageFromPublicWithExtension}`
          })`,
          backgroundRepeat: 'no-repeat',
          backgroundSize: 'cover',
          backgroundPosition: 'center',
        }}
      ></ImageWrapper>
    </Wrapper>
  );
};

export default TextAndPictureEachOther;
